.actions {
    position: relative;
    margin-left: 20px;

    @media (max-width: 768px) {
        margin-left: 0;
    }

    &__list {
        position: absolute;
        left: 50%;
        bottom: 200%;
        transform: translate(-50%, 0);
        background: #fff;
        padding: 30px;
        box-shadow: 0px 4px 15px rgba(96, 128, 158, 0.64);
        border-radius: 10px;
        transition: all .2s linear;
        opacity: 0;
        visibility: hidden;
        transform: translate(-50%, -20px);

        @media (max-width: 993px) {
            left: unset;
            right: 0;
            transform: translate(0, 0);
        }

        &.active {
            opacity: 1;
            visibility: visible;
            transform: translate(-50%, 0);

            @media (max-width: 993px) {
                transform: translate(0, 0);
            }
        }
    }

    &__item {

        &:not(:last-child) {
            margin-bottom: 20px;
        }
    }

    &__btn,
    &__option {
        white-space: nowrap;
        display: flex;
        align-items: center;
        color: var(--gray);
        transition: all .2s linear;

        &:hover {
            color: #000;
        }
    }
}

.show__select-body {
    width: 60px;
}

.show__select {
    width: 60px;
}

.pagination {
    margin-right: 20px;
}

.show {
    margin-right: 20px;

    @media (max-width: 768px) {
        flex: 1 1 auto;
        margin-right: 0;
    }
}

.form__info-block--special {
    .form__select-header {
        padding-left: 15px;

        svg {
            margin-left: 10px;
        }

        &::before {
            display: none;
        }
    }

    .form__select {
        width: 85px;
    }

    .form__select-body {
        width: 85px;
    }

    .form__btn--special {
        width: 21px;
        height: 44px;
        border: 2px solid #BDDEDA;
        border-radius: 40px;
        margin-left: 5px;
        position: relative;

        &.active {
            &::after {
                background: #FDC300;
                transform: translate(-50%, -160%);
            }
        }

        &::after {
            content: "";
            position: absolute;
            width: 10px;
            height: 10px;
            border-radius: 50%;
            left: 50%;
            transform: translate(-50%, 40%);
            top: 50%;
            background: #64B6AC;
            transition: all .2s linear;
        }
    }

    .form__btn--switcher {
        height: 44px;
        border: 2px solid #BDDEDA;
        border-radius: 40px;
        display: flex;
        align-items: center;
        justify-content: space-between;
        width: 85px;
        padding: 0 15px;

        svg {
            fill: var(--green);
            transform: rotate(-90deg);
        }

        &.active {
            span {
                &:first-of-type {
                    display: inline;
                }
                &:last-of-type {
                    display: none;
                }
            }
        }

        span {
            margin-right: 10px;

            &:first-of-type {
                display: none;
            }
        }
    }

    .form__info-block--wrapper {
        display: flex;
        align-items: center;
    }
}

.table__title {
    min-height: 20px;
}

.table {

    &.special {
        .table__special-price,
        .table__special-available {
            display: block;
        }

        .table__special {
            display: block;
            overflow: visible;
            width: 100%;
        }
    }

    &.on {
        .table__special-profit {
            display: block;
        }

        .table__special {
            display: block;
            overflow: visible;
            width: 100%;
        }
    }

    &__special {
        padding-top: 34px;
        overflow: hidden;
        display: none;
        width: 0;
    }

    &__special-list {
    }

    &__special-item {
        position: relative;
        font-size: 14px;
        color: var(--black);
        display: flex;
        align-items: center;
        padding: 10px 30px 10px 20px;
        background-color: var(--white);
        box-shadow: 0px 4px 15px -5px rgba(213, 213, 213, 0.64);
        border-radius: 20px 0 0 20px;
        margin-bottom: 10px;
        height: 60px;
        margin-right: -25px;

        &:first-of-type {
            background-color: var(--light-green);
            height: 30px;
            margin-bottom: 16px;
            border-radius: 0;

            &::before {
                content: "";
                position: absolute;
                width: 30px;
                height: 30px;
                background-color: var(--light-green);
                left: -10px;
            }
        }
    }

    &__special-price {
        position: relative;
        z-index: 1;
        margin-right: 10px;
        width: 40px;
        display: none;
    }

    &__special-available {
        width: 40px;
        display: none;
    }

    &__special-profit {
        margin-right: 10px;
        width: 60px;
        display: none;
    }

}
